

.header-container{
    width :100%;
    height :auto;
}

.header-content{
    display: grid;
    grid-template-columns:(250px auto 300px);
    @include externalPadding;
    padding-top: 40px;
    padding-bottom: 40px;
    align-items: center;
    background-color: $primary-background-white;
}

.header-content__logo{
    grid-column: 1;
    width:210px;
    height: 68px;
}
.header-content__nav{
    grid-column: 3;
    justify-self:right;
}
.header-content__nav-link{
    width:138px;
    height: 34px;
    margin-left:57px;
}